@import'variaveis';
.hero{
    max-width: 1024px;
    height: 30%;
    margin: auto;
    text-align: center;
    font-family: "Merienda", cursive;
    h2{
        text-transform: uppercase;
        margin-top: 2%;
        margin-bottom: 2%;
    }
}
.carousel-inner{

    max-width: 100%;
    height: 720px;
    object-fit: cover;
}
.carousel-item img{

    border-radius: 15px;
    width: 100%;
}